import React from 'react';
import './index.css';
import web from '../src/images/img-1.png';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/bootstrap/dist/js/bootstrap.bundle';
import { NavLink } from 'react-router-dom';

function Common(props) {
  return (
    <>
      <section id='header' className='d-flex align-items-center' >
        <div className='container-fluid  '>
          <div className='row'>
            <div className='col-10 mx-auto'>
            <div className='row'>
              <div className='col-md-6 pt-5 pt-lg-0 order-2 d-flex justify-content-center flex-column '>
                <h1>{props.name} <strong className='brand-name'>Anounomus Solution</strong> </h1>
                <h2 className='my-3'>
                  I am a developer making website and illustrations.
                </h2>
                  <div className='mt-3'>
                    <NavLink to={props.visit} className='btn-get-started' >
                        {props.btname}
                    </NavLink>
                  </div>
              </div>
                  <div className='col-lg-6 order-1 order-lg-2 header-img'>
                    <img src={props.imgsrc} className='img-fluid animated' alt='Home page'  />
                  </div>
              </div>
            </div>
          </div>
        </div>
      </section>    
    </>    
    
  );
}

export default Common;
